import React, {useContext, useEffect, useState} from 'react';
import {SurveyContext} from "./surveyContext";
import {Link} from "react-router-dom";
import OfflineVideoPlayer from "./offline-video-player";


const VideoGallery = () => {

    const {surveyCompleted} = useContext(SurveyContext);

    const [videos, setVideos] = useState([]);

    useEffect(() => {
        async function fetchCachedVideo() {
            const cacheName = 'my-video-cache'; // Replace with your cache name from the service worker
            const videoUrl1 = '/videos/video-1.mp4'; // Replace with the actual video file path
            const videoUrl2 = '/videos/video-2.mp4'; // Replace with the actual video file path
            const videoUrl3 = '/videos/video-3.mp4'; // Replace with the actual video file path
            const videoUrl4 = '/videos/video-4.mp4'; // Replace with the actual video file path

            const videos = [
                {
                    url: videoUrl1
                },
                {
                    url: videoUrl2
                },
                {
                    url: videoUrl3
                },
                {
                    url: videoUrl4
                }
            ]
            setVideos(videos)
        }

        fetchCachedVideo();
    }, []);
    return (
        <div className="grid lg:grid-cols-3 grid-cols-2 items-center justify-center h-screen">
            {surveyCompleted ? (
                    videos.map((video, index) => (
                        <OfflineVideoPlayer key={index} videoUrl={video?.url} />
                    ))) :

                <div className={"h-screen flex items-center justify-center "}>
                    <div className="bg-white rounded-lg shadow-md p-8">
                        <h2 className="text-2xl font-bold mb-4">Access Denied</h2>
                        <p className="text-gray-700">
                            Please complete the survey form to gain access to the video content.
                        </p>
                        <Link to={'/'}>
                            <button className="mt-4 bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded">
                                Go to Survey
                            </button>
                        </Link>
                    </div>


                </div>}
        </div>
    );
};

export default VideoGallery;