// FolderItem.js
import React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import { Folder, Description } from '@mui/icons-material';
import IconButton from '@mui/material/IconButton';
import { useNavigate } from 'react-router-dom';

const HorizontalBox = styled(Box)({
    display: 'flex',
    alignItems: 'center',
});

const BigFolderIcon = styled(Folder)({
    fontSize: '36px',
    color: 'yellow',
});

const BigFileIcon = styled(Description)({
    fontSize: '36px',
    color: 'yellow',
});

const FolderItem = ({ folder }) => {
    const navigate = useNavigate();

    const handleFolderClick = () => {
        navigate(`/folder/${folder.id}`);
    };

    return (
        <HorizontalBox>
            <IconButton onClick={handleFolderClick}>
                {folder.open ? <BigFolderIcon /> : <BigFolderIcon />}
            </IconButton>
            <div onClick={handleFolderClick} style={{ cursor: 'pointer' }}>
                {folder.name}
            </div>
        </HorizontalBox>
    );
};

export default FolderItem;
